export default {
	data() {
		return {
			loading: false,
			newData: [] //数据
		};
	},
	created() {
		this.getData();
	},
	methods: {
		getData() {
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/Timeline',
				success: res => {
					setTimeout(() => {
						this.loading = true
					}, 500);
					this.newData = res.data;
				}
			});
		}
	}
};
